<template>
  <div>
    <br>
    <p class="títol"><strong>RECOMANACIONS</strong></p>
    <hr class="linea-títol">
    <h1 class="pb-2">
      <icons :icon="['fas', 'award']" />
      <icons :icon="['fas', 'award']" />
    </h1>
    <p>Aquí us facilitem diverses recomanacions de diversos punts per a que et deixis portar per allò que nosaltres creiem que et pot agradar.</p>
    <p>Deixa't portar i disfruta a cegues.</p>
    <br>
    <div class="container-md">
      <b-card-group columns>
        <div v-for="proyecto in proyectos" :key="proyecto.id">
          <Card2 :Nom="proyecto.Nom" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :Descripció="proyecto.Descripció" :Informació="proyecto.Informació"></Card2>
        </div>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import Card2 from '../components/Card2.vue'
const lstProysJson = '/proyectosWD2021.json';

export default {
  props: {
    name: {
      type: String,
      default: 'Preg-freq'
    }
  },
  components: {
    Card2
  },
  data() {
    return {
      proyectos: [],
      githubPage: 1,
      page: 1,
      loading: false,
      perPage: 5
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const res = await fetch(lstProysJson);
        const val = await res.json();
        this.proyectos = val;
        console.log(val);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    }
  }
};
</script>

<style scoped>
.títol {
  font-size: 40px;
  color: #c57d56;
}
.linea-títol {
  border: 1px solid #c57d56;
  margin: 20px 0;
}
</style>
